
.nav-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-width: 100%;
  min-height: 1.6rem;
  background: #eeeeee;
}
.nav-box a {
  display: block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  min-height: 1.6rem;
  line-height: 1.6rem;
  text-decoration: none;
  color: #666;
  font-size: 0.75rem;
}
.router-link-active a {
  color: red;
}



