
.h-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 2.5rem;
  background: #ED4040;
}
h1 {
  margin: 0;
  margin-left: 0.25rem;
  margin-right: 0.5rem;
  color: #fff;
  font-size: 0;
  height: 2.5rem;
  width: 5.4rem;
  background: url(../assets/logo.png) no-repeat left;
  background-size: 5.4rem 1.35rem;
}
.txt {
  height: 1.25rem;
  width: 7rem;
  margin: auto 0;
  margin-right: 0.5rem;
  border: none;
  border-radius: 0.25rem;
  outline: none;
  font-size: 0.6rem;
  box-sizing: border-box;
  padding: 0 0.375rem;
  color: #666;
  line-height: 1.25rem;
}
.btn {
  width: 2rem;
  height: 1.25rem;
  border: none;
  margin: auto 0;
  border-radius: 0.25rem;
  outline: none;
}



